html, #root {
  margin: 0;
  padding: 0;
}

.main-page {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.main-page .divided-page {
    display: flex;
    position: relative;
    flex-direction: row;
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.divided-page .background-page {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: green;
    overflow: hidden;
}

.divided-page .background-page img{
  position: relative;
  width: 80%;
  height: 50%;
}

.divided-page  .account-page{
  position: relative;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-family: 'Montserrat' sans-serif;
}

.account-page h5{
  font-size: 25px;
  margin-top: -30px
}

.text-account{
  display: flex;
  flex-direction: row;
  position: relative;
  justify-content: center;
  width: 100%;
  height: 100%;
  align-items: center;
  flex-basis: 50%;
  padding: 5px;
  text-align: center;
  font-family: 'Montserrat' sans-serif;
}

 .login-section, .registration-section {
  text-align: center;
  padding: 5px;
  width: 70vmin;
  height: 40vmin;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: .3s all linear;
  box-shadow: 1px 1px 1px 3px rgba(3, 70, 23, 0.4);
}

.login-section {
  transform: translateX(50%);
}

.registration-section {
  width: 80vmin;
  height: 600px;
  margin-top: 5px;
  transform: translateX(100%);
   opacity: 0;
}

.registration-section.show{
  transform: translateX(-50%);
  opacity: 1;
}

 .login-section.close {
    transform: scale(0);
    opacity: 0;
}

 .login-form, .registration-form {
  width: 100%;
  padding: 0 10px;
}

.text-helper {
  margin-top: 10px;
  text-align: center;
}

.text-helper span{
  text-decoration: underline;
  cursor: pointer;
  transition: all .3s linear;
  font-weight: 500;
}
.text-helper span:hover {
  color: blue;
}


.form-control {
  width: 100% !important;
}

header {
  position: relative;
  width: 100%;
  height: 60px;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.4);
}

header .my-nav{
    position: relative;
    top: 0;
    left: 0;
    padding: 5px;
    width: 100%;
    height: 100%;
    flex-basis: 25%;
}

.my-nav img {
  position: absolute;
  width: 50px;
  height: 50px;
  object-fit: contain;
}

header .my-second-nav {
    flex: 30%;
    position: relative;
    text-align: center;
}

.my-second-nav ul {
  position: relative;
  display: inline-flex;
  justify-content: space-around;
  align-items: center;
  list-style-type: none;
  margin: 0;
  font-weight: 500;
  font-size: 18px;
  font-family: "Roboto", sans-serif;
}

.my-second-nav ul li {
  padding: 10px 15px;
  cursor: pointer;
}

header .third-nav {
  position: relative;
  flex-basis: 40%;
}

.third-nav ul {
  display: inline-flex;
  width: 100%;
  list-style-type: none;
  margin: 0;
  font-size: 18px;
  font-weight: 500;
  font-family: 'Roboto', sans-serif;
}

.third-nav ul li {
  padding: 8px 10px;
  cursor: pointer;
}

.third-nav ul li:first-child {
    cursor: default;
    font-weight: bolder;
}

li {
  transition: all .3s linear;
}
li:hover {
  color: green;
}

.my-height {
  height: 80vh !important;
}

.loading {
  position: absolute;
  width: 100%;
  height:100%;
  top:0;
  left:0;
  background: rgba(10, 1, 1, 1) 50% 50% no-repeat ;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  z-index: 90;
}

.loading h4{
  transition: all .3s linear ;
  font-weight: bold;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif sans-serif;
  animation: .5s linear infinite show-opacity;
}

@keyframes show-opacity {
    0%{
      opacity: 1;
    }
    50%{
      opacity: 0;
    }
    100%{
      opacity: 1;
    }
    
}

 .loading .spinner-grow{
  color: green !important;
  display: block !important;
  float: left !important;
  text-align: left !important;
}
.my-width {
  width: 15%;
}

.bolder-text {
  font-weight: 900 !important;
}

.my-center {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.4) 50% 50%;
  z-index: 100;
  width: 100%;
  height: 100%;
  transition: all  linear;
  transform: scale(0);
  display: none;
  opacity: 0;
}

.my-center.open{
  transform : scale(1);
  opacity: 1;
  display: flex;
}

.pointer i {
  cursor: pointer;
  font-size: 19px;
  padding: 5px 0;
  margin-bottom: 10px;
}

.main-cart-page {
  position: relative;
  padding:40px;
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items:center
}

.main-cart-page .bolder {
  font-size: 30px;
  font-weight: bolder;
  text-decoration: underline;
  font-family: Roboto, sans-serif;
}

.main-cart-page h4{
  font-weight: bolder;
  text-transform: capitalize;
}

.product-details {
  display: flex;
  flex-direction: row;
  width:100%;
  height:100%;
  justify-content:space-around;
  align-items: flex-start;
  margin-top:30px;
}

.product-page, .product-quantity, .product-price, .sub-total {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.product-quantity .quantity-product{
  display: flex;
  justify-content:center;
  margin: 3px 0;
  width:100%;
  align-items: center;
  flex-direction: row;
}

.product-quantity .quantity-product .my-button{
    flex:30%;
    width: 50px;
    text-align: center;

}

.product-quantity .quantity-product h5{
  flex: 30%;
}

.my-button button {
  border:none;
  outline:none;
  transition: all .3s linear;
  background-color: orange;
  padding:0 5px;
  text-align:center;
  color:white;
  box-shadow: -1px -1px 1px 1px rgba(70, 50, 50, 0.3);
}
.my-button:last-child button  {
  background-color:green;
}



.my-button button:hover{
  opacity: 0.6
}

.product-quantity .quantity-product h5{
  padding:0 5px;
}

.product-price {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-weight:bolder;
  font-style:italic;
  font-family:Roboto, sans-serif;
}

.total-page {
  display: flex;
  margin-top: 10px;
  border-top: 3px solid black;
  border-bottom: 3px solid black;
  width: inherit;
  justify-content: center;
  width:100% ;
  align-items: center;
}

 .order-detail {
  margin-top: 30px;
  display: flex;
  flex-direction:row;
  padding: 1px;
  width: 90%;
  justify-content: flex-end !important;
  align-items: center  !important;
}

.order-detail button {
 margin:10px;
 float: left !important;
}

.order-page {
  display: flex;
  width: 100%;
  padding: 40px;
  justify-content:space-between;
  align-items:flex-start;
}

.address-page{
  padding: 10px;
  width: 100%;
  height: 100%;
  flex-basis: 60%;
  border-right: 1px solid black;
}
.address-page .form-page {
  width :100%;
  height: 100%;
}

.address-page h4{
  text-align: center;
  text-decoration: underline green 6px dotted;
  font-weight: bolder;
  margin-bottom: 30px;
}

.order-price-page {
  flex-basis: 30%;
  padding: 10px;
  text-align: center;
}

.order-price-page h4{
  text-decoration: underline green 6px;
  text-decoration-style: dotted;
}

.border-with{
  border-right: 3px solid black;
  height: 600px;
  margin-left:30px;
}